
import { Options, Vue } from "vue-class-component";
import pic from "./pic.vue";
@Options({
  components: {
    pic
  }
})
export default class Home extends Vue {
  positionTop: number = -120;
  speed: number = 1;
  timer: any;
  isCir: boolean = false;
  boomCir: boolean = false;
  showPic: boolean = false;
  cir: boolean = true;
  opacity: number = 0;
  mounted() {
    this.timer = setInterval(() => {
      this.positionTop = this.positionTop += this.speed;
      if (this.positionTop % 100 === 0) {
        this.speed += 1;
      }
      if (window.innerHeight - 180 - this.positionTop <= 0) {
        window.clearInterval(this.timer);
        this.isCir = true;
        this.cirToTop().then(() => {
          this.cirBoom();
        });
      }
    }, 10);
  }
  cirToTop(): any {
    return new Promise((resolve: any, reject) => {
      this.timer = setInterval(() => {
        this.positionTop -= this.speed;
        if (window.innerHeight / 2 >= this.positionTop + 90) {
          window.clearInterval(this.timer);
          resolve();
        }
      }, 10);
    });
  }
  cirBoom(): any {
    this.boomCir = true;
    setTimeout(() => {
      this.cir = false;
    }, 3800);
    setTimeout(() => {
      this.showPic = true;
      setTimeout(() => {
        this.opacity = 1;
      }, 1000);
    }, 1000);
  }
}
